
import Alert from 'vux/src/components/alert/index.vue'
import md5 from 'vux/src/tools/md5/index.js'
import Icon from 'vux/src/components/icon/index.vue'

export default {
name: 'Uploader',
components: {
Alert,
Icon
},
props: {
isShow: {
type: Boolean,
default: true
},
myimgs: {
type: Array,
default: () => {
return []
}
},
max: {
type: Number,
default: 3
},
showNum: {
type: Boolean,
default: true
}
},
data () {
return {
imgs: [],
isMax: true,
lastImg: [],
uploadImgs: []
}
},
mounted () {
this.imgs = this.myimgs
},
watch: {
imgs (newVal) {
if (newVal.length === this.max) {
this.isMax = false
}
this.$emit('upload-change', newVal)
},
myimgs (newVal) {
this.imgs = newVal
}
},
methods: {
delete_img (item) {
this.$refs.chooseImg.value = '' // upload
this.imgs.splice(item, 1)
if (this.imgs.length < this.max) {
this.isMax = true
}
},
add_img (event) {
let reader = new FileReader()
console.log(reader)
let img1 = event.target.files[0]
if (!/\.(gif|jpg|jpeg|png|GIF|JPG|PNG)$/.test(img1.name)) {
this.$vux.alert.show({
title: '',
content: 'gifjpgjpegpng'
})
return false
}








this.lastImg.push(img1.name)
let picName = img1.lastModified + md5(img1.name)
this.getClient(img1, picName)
},

getClient (file, picName) {
this.$axios.get('/api/picManage/getStsInfo').then((response) => {
console.log(response.data.code === '200')
if (response.data.code === '200') {
this.uploadFile(response.data.data, file, picName)
} else {
this.$vux.alert.show({
title: '',
content: ''
})
}
}).catch((error) => {
this.$vux.alert.show({
title: '',
content: ''
})
console.log('error:' + error)
})
},

uploadFile (data, file, name) {
let _this = this
var OSS = window.OSS.Wrapper
var client = new OSS({
region: 'oss-cn-hangzhou',
accessKeyId: data.accessKeyId,
accessKeySecret: data.accessKeySecret,
stsToken: data.securityToken,
bucket: 'baoyitech'
})
console.log(name, file)
return client.multipartUpload(name, file).then((res) => {
if (_this.imgs.length > _this.max - 1) {
_this.$vux.alert.show({
title: '',
content: '' + _this.max + ''
})
} else {
_this.imgs.push(`http://baoyitech.oss-cn-hangzhou.aliyuncs.com/${res.name}`)
if (_this.imgs.length > _this.max - 1) {
_this.isMax = false
}
}
}).catch(() => {
this.$vux.alert.show({
title: '',
content: ''
})
})
}
}
}



